}

.btn-1 {
  text-transform: uppercase;
  letter-spacing: 2px;
  color: #fff;
  outline: 2px solid #fff;
  padding: 30px 60px;
  font-family: Sumana, serif;
  text-decoration: none;
  transition: color 1s;
  display: inline-block;
  position: relative;
  overflow: hidden;
}

.btn-1:hover {
  color: #526e6e;
}

.btn-1:before {
  content: "";
  width: 0;
  height: 100%;
  z-index: -1;
  background-color: #fff;
  transition: width 1s;
  position: absolute;
  top: 0;
  left: -50px;
  transform: skewX(35deg);
}

.btn-1:hover:before {
  width: 150%;
}

.btn-2 {
  color: #fff;
  border: 3px solid #fff;
  padding: 20px 50px;
  font-family: Sumana, serif;
  font-size: 25px;
  text-decoration: none;
  display: inline-block;
  position: relative;
}

.btn-2:before, .btn-2:after {
  content: "";
  width: 40px;
  height: 40px;
  border: inherit;
  transition: all .5s;
  position: absolute;
}

.btn-2:before {
  border-width: 3px 0 0 3px;
  top: -15px;
  left: -15px;
}

.btn-2:after {
  border-width: 0 3px 3px 0;
  bottom: -15px;
  right: -15px;
}

.btn-2:hover:before, .btn-2:hover:after {
  width: calc(100% + 30px);
  height: calc(100% + 30px);
}

.main {
  width: 200px;
  height: 8px;
  z-index: 2;
  border: 1px solid #000;
  border-radius: 10px;
}

.box {
  width: 200px;
  height: 6px;
  background-color: #000;
  border-radius: 10px;
  animation: anima 4s infinite;
}

@keyframes anima {
  from {
    width: 0;
  }

  to {
    width: 200px;
  }
}

.spinner {
  width: 80px;
  height: 80px;
  border-radius: 50%;
  position: relative;
}

.spinner:before, .spinner:after {
  content: "";
  border-radius: inherit;
  position: absolute;
}

.spinner:before {
  width: 100%;
  height: 100%;
  background-image: linear-gradient(0deg, #f0c 0%, #339 100%);
  animation: spin .5s linear infinite;
}

.spinner:after {
  width: 85%;
  height: 85%;
  background-color: #526e6e;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

.typing-demo {
  width: 22ch;
  white-space: nowrap;
  border-radius: 3px solid;
  animation: typing 2s steps(22, end), blink .5s step-end infinite alternate;
  overflow: hidden;
}

@keyframes typing {
  from {
    width: 0;
  }
}

@keyframes blink {
  50% {
    border-color: rgba(0, 0, 0, 0);
  }
}

